import React from 'react';
import { SendEmailDriversEd } from '../components/helpers/Emailer';
import Policies from '../data/policies.pdf';
import Layout from '../components/layout/Layout';
import Textbox from '../components/forms/Textbox';
import Dropdown from '../components/forms/Dropdown';

const packages = [
    {
        title: 'Full Package',
        value: '699.00'
    },
    {
        title: '30 Hours Classroom Lessons',
        value: '199.00'
    },
    {
        title: '12 Hours Driving Instruction',
        value: '540.00'
    }
];

export default class RegisterDriversEd extends React.Component {

    constructor(props) {
        super(props)
        var initialForm = {
            'selectedPackage': packages[0].title,
            'selectedPrice': packages[0].value
        }

        this.state = ({ packageList: packages, currentForm: initialForm, permitFile: '' });
        this.dropdownChanged = this.dropdownChanged.bind(this)
        this.formSubmitted = this.formSubmitted.bind(this)
        this.handleFormChange = this.handleFormChange.bind(this)
        this.handleCheckboxChange = this.handleCheckboxChange.bind(this)
        this.updateNumberFormLogic = this.updateNumberFormLogic.bind(this)
        this.autonumIncrement = this.autonumIncrement.bind(this)
        this.compressImage = this.compressImage.bind(this)
        this.handleFileRead = this.handleFileRead.bind(this)
    }

    handleCheckboxChange(event) {
        var currentFormCopy = this.state.currentForm
        currentFormCopy[event.target.name] = event.target.checked
        this.setState({ currentForm: currentFormCopy })
    }

    updateNumberFormLogic(event, startField, endField, digit) {
        if (event.target.name === startField && event.target.value.length > digit - 1) {
            var textVal = event.target.value.substring(0, digit)
            var currentFormCopy = this.state.currentForm
            currentFormCopy[startField] = textVal
            this.setState({ currentForm: currentFormCopy })
            document.getElementById(endField).focus()
        }
    }

    autonumIncrement(event) {
        //now for changing cursor for phone number boxes
        this.updateNumberFormLogic(event, 'home_phone1', 'home_phone2', 3)
        this.updateNumberFormLogic(event, 'home_phone2', 'home_phone3', 3)
        this.updateNumberFormLogic(event, 'home_phone3', 'cell_phone1', 4)
        this.updateNumberFormLogic(event, 'cell_phone1', 'cell_phone2', 3)
        this.updateNumberFormLogic(event, 'cell_phone2', 'cell_phone3', 3)
        this.updateNumberFormLogic(event, 'cell_phone3', 'parent_name', 4)
        this.updateNumberFormLogic(event, 'parent_phone1', 'parent_phone2', 3)
        this.updateNumberFormLogic(event, 'parent_phone2', 'parent_phone3', 3)
        this.updateNumberFormLogic(event, 'parent_phone3', 'best_time_to_call', 4)
    }

    handleFormChange(event) {
        var currentFormCopy = this.state.currentForm
        currentFormCopy[event.target.name] = event.target.value
        this.setState({ currentForm: currentFormCopy })
        this.autonumIncrement(event)
    }

    handleFileRead = (e) => {
        const content = this.state.fileReader.result;
        this.setState({
            imageContent: content
        })
    }

    fileBrowserChanged = event => {
        var fileSelected = event.target.files[0]
        this.state.fileReader.onloadend = this.handleFileRead
        this.state.fileReader.readAsText(fileSelected)

        var currentFormCopy = this.state.currentForm
        if (fileSelected && fileSelected['type'].split('/')[0] === 'image') {
            currentFormCopy[event.target.name] = event.target.value
        } else {
            alert('Non-image file detected. Please select an image and try again. You can also type in your permit number if you have problems.')
            currentFormCopy[event.target.name] = ''
        }
        this.setState({ currentForm: currentFormCopy })
    }

    dropdownChanged(event) {
        var val = this.state.packageList.find(o => o.title === event.target.value).value
        var currentFormCopy = this.state.currentForm
        currentFormCopy['selectedPackage'] = event.target.value
        currentFormCopy['selectedPrice'] = val
        this.setState({ currentForm: currentFormCopy })
    }

    compressImage() {
        try {
            var fileContent = this.state.imageContent
            var fileContentEncoded = new Buffer(fileContent).toString('base64')
        } catch (e) {
            console.log('exception: ', e)
        }
    }

    formSubmitted(event) {
        console.log(this.state.currentForm)
        //compress image
        var compressedImage = this.compressImage()
        var sentEmail = SendEmailDriversEd(this.state.currentForm)
        if (sentEmail === true) {
            alert('Data saved! Please give us a call if you do not hear back within 24 hours')
            document.getElementById('formSubmit').disabled = true
        }
        else {
            alert('Error submitting form! Please try again! If error persists, please give us a call!')
        }
        event.preventDefault()
    }

    render() {
        let options = ["option1", "option2", "option3"]
        return (
            <Layout>
                <h2> Register for Driver's ED Program </h2><br />
                <form class="w-full max-w-sm">
                    <Textbox label="Full Name" value="" placeholder="Enter Full Name" for="full-name" required="true" />
                    <Textbox label="Street Address" value="" placeholder="Enter Street Address" for="street-address" required="true" />
                    <Textbox label="City" value="" placeholder="Enter City" for="city" required="true" />
                    <Textbox label="Zip Code" value="" placeholder="Enter Zip Code" for="zip-code" required="true" />

                    <Textbox label="Student Email" value="" placeholder="Enter Student Email" for="student-email" required="true" />
                    <Textbox label="Student Cell Phone" value="" placeholder="Enter Cell Phone" for="student-cell-phone" required="true" />
                    <Textbox label="Home Phone" value="" placeholder="Enter Home Phone" for="home-phone" />

                    <Textbox label="Parent Name" value="" placeholder="Enter Parent Name" for="parent-name" />
                    <Textbox label="Parent Email" value="" placeholder="Enter Parent Email" for="parent-email" />
                    <Textbox label="Parent Phone" value="" placeholder="Enter Parent Phone" for="parent-phone" />

                    <Textbox label="Best Time To Call" value="" placeholder="Best Time To Call" for="best-time-to-call" />
                    <Textbox label="Learner's Permit #" value="" placeholder="Enter Learner's Permit #" for="learners-permit" />

                    <Textbox label="Date of Birth" value="" placeholder="Enter Date of Birth" for="date-of-birth" required="true" />
                    <Textbox label="Starting Date" value="" placeholder="Enter Starting Date" for="starting-date" />
                    <Textbox label="Comments" value="" placeholder="Enter Comments" for="comments" />

                    <h2>Selected Package</h2>
                    <br />
                    <Dropdown options={options} />
                    <br />

                    <p>I have reviewed and agree upon the price shown above! (Credit / Debit card payments done via PayPal will be subject to 3% service charge).</p>
                    <br />
                    <p>I have read and agree with the Terms and Conditions.</p>

                    <div class="md:flex md:items-center">
                        <div class="md:w-1/3"></div>
                        <div class="md:w-2/3">
                            <button class="shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded" type="button">Register</button>
                        </div>
                    </div>
                </form>
            </Layout>
        );
    }
}